import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-module-person-course',
  templateUrl: './module-person-course.component.html',
  styleUrls: ['./module-person-course.component.css']
})
export class ModulePersonCourseComponent implements OnInit {
  public items;
  public course;
  public incourse;
  public relator;
  public persontherapy;
  public therapy;
  public newpayment;
  public emptyCamp = false;
 
  constructor(
    private route: ActivatedRoute,private router: Router,private http: HttpClient
  ) { }

  ngOnInit(): void {
    this.inittherapy()
    this.getItems()
    this.getCourses()
    this.getRelator()
    this.gettherapy()
    this.initpayment()
  }
  inittherapy(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('rut');
    this.persontherapy = {idperson:firstParam,idtherapist:"",date:"",hour:"",description:"",cost:0}
  }
  getItems(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('rut');
    if(firstParam){
      this.http.post<any>('https://luzvioleta.herokuapp.com/detallePerson',{rut:firstParam}).subscribe(data => {
        this.items = data[0];
      });
    }else{
      this.items = {rut:null, name:'', lastname:'', birth:'', address:''}
    }
    
  }
  gettherapy(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('rut');
    this.http.post<any>('https://luzvioleta.herokuapp.com/selecttherapybyID',{ID:firstParam}).subscribe(data => {
      this.therapy= data;
    });
  }
  setcourse(course){
    const firstParam: string = this.route.snapshot.queryParamMap.get('rut');
    this.http.post<any>('https://luzvioleta.herokuapp.com/inserthistory',{idcourse:course.ID, idperson:firstParam}).subscribe(data => {
      this.getCourses()
    });
  }
  getcoursesall(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/detallecourseall').subscribe(data => {
      this.incourse = data;
    });
  }
  getCourses(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('rut');
    this.http.post<any>('https://luzvioleta.herokuapp.com/detallePersoncourse',{rut:firstParam}).subscribe(data => {
      this.course = data;
    });
  }
  getRelator(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectEmployee').subscribe(data => {
      this.relator = data;
    });
  }
  setTherapy(){

    if(this.persontherapy.idtherapist != "" && this.persontherapy.hour != "" && this.persontherapy.date != ""){
      this.emptyCamp = false
      
      this.http.post<any>('https://luzvioleta.herokuapp.com/inserttherapy',this.persontherapy).subscribe(data => {
        this.inittherapy()
        this.gettherapy()
      });

    }else{
      this.emptyCamp = true
     
    }
  
  }
  changeItem(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('rut');
   
    if(this.items.rut != "" && this.items.address != "" && this.items.birth != "" && this.items.lastname != "" && this.items.name){
      this.emptyCamp = false
      if(firstParam){
        this.http.post<any>('https://luzvioleta.herokuapp.com/updatePerson',this.items).subscribe(data => {
          this.getItems()
        });
      }else{
        this.http.post<any>('https://luzvioleta.herokuapp.com/insertPerson',this.items).subscribe(data => {
          console.log(data)
          if(data){
            this.router.navigate(['personModule/courses'], { queryParams: { rut: this.items.rut} });
          }
          
        });
      }

    }else{
      this.emptyCamp = true
 
    }
    
    
  }
  initpayment(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('rut');
    this.newpayment = {method:"", receipt_number:"", amount:0, date:"", personid:firstParam}
  }
  setpayment(){

    if(this.newpayment.receipt_number != "" && this.newpayment.amount > 0 && this.newpayment.date != "" && this.newpayment.method != ""){
      this.emptyCamp = false
      this.http.post<any>('https://luzvioleta.herokuapp.com/insertpayment',this.newpayment).subscribe(data => {
        this.initpayment()
      });
      
    }else{
      this.emptyCamp = true
    
    }
  
  }
  deletehistory(IDhistory){
    var r = confirm("Desea eliminar?");
    if (r == true) {
      this.http.post<any>('https://luzvioleta.herokuapp.com/deletehistory',{ID:IDhistory}).subscribe(data => {
        this.getCourses()
      });
    }
  }
  deleteTherapy(IDtherapy){
    var r = confirm("Desea eliminar?");
    if (r == true) {
      this.http.post<any>('https://luzvioleta.herokuapp.com/deteletherapy',{ID:IDtherapy}).subscribe(data => {
        this.inittherapy()
        this.gettherapy()
      });
    }
  }
}
