import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-therapy',
  templateUrl: './therapy.component.html',
  styleUrls: ['./therapy.component.css']
})
export class TherapyComponent implements OnInit {
  public therapy;
  public therapy2;
  public relator;
  public person;
  public persontherapy;
  public emptyCamp = false;
  public tosearch;
  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.inittherapy()
    this.getTherapy()
    this.getRelator()
    this.getPerson()
  }
  inittherapy(){
    this.persontherapy = {idperson:"",idtherapist:"",date:"",hour:"",description:"",cost:0}
  }
  getPerson(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectPerson').subscribe(data => {
      this.person = data;
    });
  }
  getTherapy(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selecttherapy').subscribe(data => {
      this.therapy = data;
      this.therapy2 = data;
    });
  }
  search(){
    let i;

      if(this.tosearch == ""){
        this.therapy = this.therapy2
  
      }else{
        this.therapy = []
        for(i = 0; i < this.therapy2.length; i++){
          
          if(this.tosearch.toLowerCase() == this.therapy2[i].idperson.toLowerCase() || this.tosearch.toLowerCase() == this.therapy2[i].idtherapist.toLowerCase()){
            
            this.therapy.push(this.therapy2[i])
  
          }
  
        }
      }
  }
  getRelator(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectEmployee').subscribe(data => {
      this.relator = data;
    });
  }
  setTherapy(){
    if(this.persontherapy.idperson != "" &&this.persontherapy.idtherapist != "" && this.persontherapy.hour != "" && this.persontherapy.date != ""){
      this.emptyCamp = false
      
      this.http.post<any>('https://luzvioleta.herokuapp.com/inserttherapy',this.persontherapy).subscribe(data => {
        this.inittherapy()
        this.getTherapy()
      });

    }else{
      this.emptyCamp = true
      console.log("llene todos los campos")
    }
   
  }
  deleteTherapy(IDtherapy){
    var r = confirm("Desea eliminar?");
    if (r == true) {
      this.http.post<any>('https://luzvioleta.herokuapp.com/deteletherapy',{ID:IDtherapy}).subscribe(data => {
        this.inittherapy()
        this.getTherapy()
      });
    }
  }
}
