import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-module-payment',
  templateUrl: './module-payment.component.html',
  styleUrls: ['./module-payment.component.css']
})
export class ModulePaymentComponent implements OnInit {
  public allpay;
  public allpay2;
  public alldef;
  public alldef2;
  public person;
  public newpayment;
  public emptyCamp = false;
  public tosearch;
  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.getpayall()
    this.getPerson()
    this.getalldef()
    this.initpayment()
  }
  initpayment(){
    this.newpayment = {method:"", receipt_number:"", amount:0, date:"", personid:""}
  }
  getpayall(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectPay').subscribe(data => {
      this.allpay = data;
      this.allpay2 = data;
    });
  }
  getalldef(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectdefaulter').subscribe(data => {
      this.alldef = data;
      this.alldef2 = data;
    });
  }
  getPerson(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectPerson').subscribe(data => {
      this.person = data;
    });
  }
  search(x){
    let i;

    if(x == 1){
      if(this.tosearch == ""){
        this.allpay = this.allpay2
  
      }else{
        this.allpay = []
        for(i = 0; i < this.allpay2.length; i++){
          
          if(this.tosearch.toLowerCase() == this.allpay2[i].personid.toLowerCase() || this.tosearch.toLowerCase() == this.allpay2[i].receipt_number.toLowerCase()){
            this.allpay.push(this.allpay2[i])
          }
        }
      }

    }else{
      if(this.tosearch == ""){
        this.alldef = this.alldef2
  
      }else{
        this.alldef = []
        for(i = 0; i < this.alldef2.length; i++){  
          if(this.tosearch.toLowerCase() == this.alldef2[i].idperson.toLowerCase()){
            this.alldef.push(this.alldef2[i])
          }
        }
      }
    }
  }
  setpayment(){
    if(this.newpayment.method != "" && this.newpayment.receipt_number != "" && this.newpayment.date != "" && this.newpayment.personid != "" && this.newpayment.amount > 0){
      this.emptyCamp = false
      this.http.post<any>('https://luzvioleta.herokuapp.com/insertpayment',this.newpayment).subscribe(data => {
        this.initpayment()
        this.getpayall()
        this.getalldef()
      });

    }else{
    
      this.emptyCamp = true
    }

    
  }
  deletepayment(IDpayment){
    var r = confirm("Desea eliminar?");
    if (r == true) {
      this.http.post<any>('https://luzvioleta.herokuapp.com/detelepayment',{ID:IDpayment}).subscribe(data => {
        this.initpayment()
        this.getpayall()
        this.getalldef()
      });
    }
  }

}
