import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-module-person-detail',
  templateUrl: './module-person-detail.component.html',
  styleUrls: ['./module-person-detail.component.css']
})
export class ModulePersonDetailComponent implements OnInit {
  public items = [];
  public items2 = []
  public employee = [];
  public employee2 = []
  public selectEmployee;
  public iscreate;
  public emptyCamp = false;
  public tosearch;
  constructor(private http: HttpClient) { }

  ngOnInit(): void {
   this.getItems()
   this.getEmployee()
   this.selectEmployee = {rut:null,name:"",lastname:"",speciality:""};
  }
  search(x){
    let i, word2, word1;
    if(this.tosearch.split(" ")[1] != null){
      word1 = this.tosearch.split(" ")[0]
      word2 = this.tosearch.split(" ")[1]

    }else{
      word1 = this.tosearch
      word2 = "undefined"
    }

    if(x == 1){
      if(this.tosearch == ""){
        this.items = this.items2
  
      }else{
        this.items = []
        for(i = 0; i < this.items2.length; i++){
          
          if(word1.toLowerCase() == this.items2[i].rut.toLowerCase() || word1.toLowerCase() == this.items2[i].name.toLowerCase() || word1.toLowerCase() == this.items2[i].lastname.toLowerCase() || word2.toLowerCase() == this.items2[i].lastname.toLowerCase()){
            
            this.items.push(this.items2[i])
  
          }
  
        }
      }

    }else{
      if(this.tosearch == ""){
        this.employee = this.employee2
  
      }else{
        this.employee = []
        for(i = 0; i < this.employee2.length; i++){
          
          if(word1.toLowerCase() == this.employee2[i].speciality.toLowerCase() || word1.toLowerCase() == this.employee2[i].rut.toLowerCase() || word1.toLowerCase() == this.employee2[i].name.toLowerCase() || word1.toLowerCase() == this.employee2[i].lastname.toLowerCase() || word2.toLowerCase() == this.employee2[i].lastname.toLowerCase()){
            
            this.employee.push(this.employee2[i])
  
          }
  
        }
      }

    }

  }
  getItems(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectPerson').subscribe(data => {
      this.items = data;
      this.items2 = data;
    });
  }
  getEmployee(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectEmployee').subscribe(data => {
      this.employee = data;
      this.employee2 = data;
    });
  }
  getselectemployee(emp){
    this.selectEmployee = {rut:null,name:"",lastname:"",speciality:""};
    if(emp){
      this.selectEmployee =  emp
    }
    this.iscreate =emp
    
  }
  setEmployee(){

    if(this.selectEmployee.rut != "" && this.selectEmployee.name != "" && this.selectEmployee.lastname != "" && this.selectEmployee.speciality != ""){
      this.emptyCamp = false
      if(this.iscreate){
        this.http.post<any>('https://luzvioleta.herokuapp.com/updateEmploye',this.selectEmployee).subscribe(data => {
          this.getEmployee()
        });
      }else{
        this.http.post<any>('https://luzvioleta.herokuapp.com/insertEmployee',this.selectEmployee).subscribe(data => {
          this.getEmployee()
        });
      }

    }else{
      this.emptyCamp = true
    
    }
   
   
      
  }
}
