import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-course-module',
  templateUrl: './course-module.component.html',
  styleUrls: ['./course-module.component.css']
})
export class CourseModuleComponent implements OnInit {
  public items;
  public items2;
  public tosearch;
  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.getItems()
  }
  getItems(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectCourse').subscribe(data => {
      this.items = data;
      this.items2 = data;
    });
  }
  search(){
    let i;

      if(this.tosearch == ""){
        this.items = this.items2
  
      }else{
        this.items = []
        for(i = 0; i < this.items2.length; i++){
          
          if(this.tosearch.toLowerCase() == this.items2[i].name.toLowerCase()){
            
            this.items.push(this.items2[i])
  
          }
  
        }
      }
  }
  
}
